@use "base";

.homes {
	grid-column: center-start / center-end;
	margin: 15rem 0;

	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
	grid-gap: 7rem;
}

.home {
	background-color: base.$color-grey-light-1;

	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 3.35rem;

	&__img {
		width: 100%;
		grid-column: 1/ -1;
		grid-row: 1 / 2;
		z-index: 1;
	}

	&__like {
		grid-row: 1 / 2;
		grid-column: 2 / 3;

		justify-self: end;

		fill: base.$color-primary;
		width: 2.25rem;
		height: 2.25rem;
		z-index: 2;
		margin: 1rem;
	}

	&__name {
		grid-column: 1/ -1;
		grid-row: 1 / 2;

		justify-self: center;
		align-self: end;

		width: 80%;
		font-family: base.$font-display;
		font-size: 1.6rem;
		text-align: center;
		padding: 1.25rem;
		background-color: base.$color-secondary;
		color: #fff;
		font-weight: 400;
		z-index: 3;
		transform: translateY(50%);
	}

	&__location,
	&__rooms {
		margin-top: 2.5rem;
	}

	&__location,
	&__rooms,
	&__area,
	&__price {
		font-size: 1.5rem;
		margin-left: 2rem;

		display: flex;
		align-items: center;
		svg {
			fill: base.$color-primary;
			width: 2rem;
			height: 2rem;
			margin-right: 1rem;
		}

		p {
		}
	}

	&__btn {
		grid-column: 1/ -1;
	}
}
