@use "base";

.story {
	&__pictures {
		grid-column: full-start / col-end 4;

		display: grid;
		grid-template-rows: repeat(6, 1fr);
		grid-template-columns: repeat(6, 1fr);
		align-items: center;

		background-image: linear-gradient(
				rgba(base.$color-primary, 0.5),
				rgba(base.$color-primary, 0.5)
			),
			url(../img/back.jpg);
		background-size: cover;

		@media only screen and (max-width: base.$bp-medium) {
			grid-column: 1 / -1;
			padding: 6rem;
		}
	}

	&__img--1 {
		width: 100%;
		grid-row: 2 / 6;
		grid-column: 2 / 6;
		box-shadow: 0 2rem 5rem rgba(#000, 0.1);

		@media only screen and (max-width: base.$bp-medium) {
			grid-column: 1 / 5;
			grid-row: 1 / -1;
		}
	}

	&__img--2 {
		width: 115%;
		grid-row: 4 / 6;
		grid-column: 4 / 7;
		z-index: 20;
		box-shadow: 0 2rem 5rem rgba(#000, 0.2);

		@media only screen and (max-width: base.$bp-medium) {
			grid-row: 1 / -1;
			width: 100%;
		}
	}

	&__content {
		grid-column: col-start 5 / full-end;

		padding: 6rem 8vw;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;

		@media only screen and (max-width: base.$bp-medium) {
			grid-column: 1 / -1;
			grid-row: 5 / 6;
		}
	}

	&__text {
		font-size: 1.5rem;
		font-style: italic;
		margin-bottom: 4rem;
	}
}
