@use "base";

%heading {
	font-family: base.$font-display;
	font-weight: 400;
}

.heading-1 {
	@extend %heading;

	font-size: 4.5rem;
	color: base.$color-grey-light-1;
	line-height: 1;
}
.heading-2 {
	@extend %heading;

	font-size: 4rem;
	font-style: italic;
	line-height: 1;

	&--light {
		color: base.$color-grey-light-1;
	}

	&--dark {
		color: base.$color-grey-dark-1;
	}
}
.heading-3 {
	@extend %heading;

	font-size: 1.6rem;
	color: base.$color-primary;
	text-transform: uppercase;
}
.heading-4 {
	@extend %heading;

	font-size: 1.9rem;

	&--light {
		color: base.$color-grey-light-1;
	}

	&--dark {
		color: base.$color-grey-dark-1;
	}
}

.btn {
	background-color: base.$color-primary;
	color: #fff;
	border: none;
	border-radius: 0;
	font-family: base.$font-display;
	text-transform: uppercase;
	padding: 1.8rem 3rem;
	cursor: pointer;
	transition: all 0.2s;

	&:hover {
		background-color: base.$color-primary-dark;
	}
}

// HELPER CLASS
.mb-sm {
	margin-bottom: 2rem;
}

.mb-md {
	margin-bottom: 3rem;
}

.mb-lg {
	margin-bottom: 4rem;
}

.mb-hg {
	margin-bottom: 8rem;
}
