@use "base";

.header {
	grid-column: full-start / col-end 6;

	background-color: base.$color-grey-dark-1;
	background-image: linear-gradient(
			rgba(base.$color-secondary, 0.93),
			rgba(base.$color-secondary, 0.93)
		),
		url(../img/hero.jpeg);
	background-size: cover;
	background-position: center;
	padding: 8rem;
	padding-top: 4rem;

	display: grid;
	grid-template-rows: 1fr min-content minamx(6rem, min-content) 1fr;
	grid-template-columns: minmax(min-content, max-content);
	justify-content: center;
	grid-row-gap: 1.5rem;

	@media only screen and (max-width: base.$bp-medium) {
		grid-column: 1 / -1;
	}

	@media only screen and (max-width: base.$bp-small) {
		padding: 5rem;
	}

	&__logo {
		justify-self: center;
		height: 3rem;
	}

	&__btn {
		align-self: start;
		justify-self: start;
	}

	&__seenon-text {
		display: grid;
		grid-template-columns: 1fr max-content 1fr;
		align-items: center;
		grid-column-gap: 1.5rem;

		font-size: 1.6rem;
		color: base.$color-grey-light-2;

		&::before,
		&::after {
			content: "";
			height: 1px;
			display: block;
			background-color: currentColor;
		}
	}

	&__seenon-logos {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		justify-items: center;
		align-items: center;
		grid-column-gap: 3rem;

		img {
			max-height: 2.5rem;
			max-width: 100%;
			filter: brightness(70%);
		}
	}
}
