@use "base";

.footer {
	background-color: base.$color-secondary;
	grid-column: full-start / full-end;

	padding: 8rem;
}

.nav {
	list-style: none;

	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
	align-items: center;
	grid-gap: 1.2rem;

	&__link:link,
	&__link:visited {
		font-size: 1.4rem;
		color: #fff;
		text-decoration: none;
		font-family: base.$font-display;
		text-transform: uppercase;
		text-align: center;
		padding: 1rem;
		display: block;
		transition: all 0.3s;
	}

	&__link:hover {
		background-color: rgba(#fff, 0.05);
		transform: translateY(-5px);
	}
}

.copyright {
	font-size: 1.4rem;
	color: base.$color-grey-light-2;
	margin-top: 6rem;
	text-align: center;
}
